<style lang="less">
</style>
<template>
  <a-card style="margin-bottom:10px;">
    <jsonview :json='jsonFormData'></jsonview>
  </a-card>
</template>

<script>
import {JeecgListMixin} from "@/mixins/JeecgListMixin"
import jsonview from "@/components/jsonview";

export default {
  name: "table-form",
  mixins: [JeecgListMixin],
  components: {
    jsonview
  },
  props: {
    formDetail: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      jsonFormData: {}
    }
  },
  created() {
  },
  mounted() {
    this.jsonParse()
  },
  watch: {
    formDetail: function (newval, oldName) {
      console.log("value", newval, oldName)
    }
  },
  methods: {
    jsonParse() {
      if (this.formDetail) {
        this.jsonFormData = Object.assign({}, this.formDetail)
        if (this.jsonFormData.value) {
          this.jsonFormData.value = JSON.parse(this.jsonFormData.value)
        }
        if (this.jsonFormData.ext_value) {
          this.jsonFormData.ext_value = JSON.parse(this.jsonFormData.ext_value)
        }
      }
    }
  }
}
</script>
